// TODO: @Lukas remove this
export interface ISnapshot {
  id: string;
  date: Date;
  tags: string[];
  /**
   * Paths included in the backup
   */
  paths: string[];
}

export interface ISnapshotResponse {
  snapshots: ISnapshot[];
}

export interface ISnapshotDetails extends ISnapshot {
  /**
   * All individual files included in the snapshot. PATH SEPARATOR MUST BE `/`
   * All entries must be ordered by folder and parent folders have to be
   * included (eg ["/home", "/home/user1", "/home/user1/.bashrc"])
   */
  files: ({ path: string })[];
}

export interface ISnapshotDetailsResponse {
  details: ISnapshotDetails;
}

export interface IRestoreSnapshotArgs {
  snapshotId: string;
  selectedPaths: string[];
  restorePath: string;
}
